body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

.main-container {
  min-height: 100vh;
  background-color: rgb(227, 245, 255);
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.add-column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 20vh;
  width: 30vw;
}